import React, {PureComponent} from 'react';
import './DialogControl.css';

class DialogControl extends PureComponent{
    constructor(props){
        super(props);

        //this.onUpdate = this.onUpdate.bind(this);
    }

    shouldComponentUpdate(nextProps, nextState){
        if (nextProps.chat !== this.props.chat){
            return true;
        }
        if (nextProps.isSelected !== this.props.isSelected){
            return true;
        }

        return false;
    }

    handleClick(){
        this.props.onClick(this.props.chat);
    }

    getChatContent(chat){
        if (!chat) return '[chat undefined]';
        if (!chat.last_message) return '[last_message undefined]';
        const content = chat.last_message.content;
        if (!content) return '[content undefined]';

        switch (content['@type']) {
            case 'messageText':
                return content.text.text;
            case 'messageDocument':
                return 'document';
            default:
                return '[unsupported content ' + content['@type'] + ']';
        }
    }

    getChatLetters(chat){
        if (!chat) return null;
        if (!chat.title) return null;
        if (chat.title.length === 0) return null;

        let split = chat.title.split(' ');
        if (split.length > 1){
            return split[0].charAt(0) + split[1].charAt(0);
        }

        return chat.title.charAt(0);
    }

    render(){
        const chat = this.props.chat;
        const content = this.getChatContent(chat);
        const letters = this.getChatLetters(chat);

        const backgroundColor = 'user_bgcolor_' + (Math.abs(chat.id) % 8 + 1);
        const photoClasses = `${backgroundColor} photo`;

        const photo = chat.blob !== undefined ?
            (<img className={photoClasses} src={URL.createObjectURL(chat.blob)} alt="" />) :
            (<div className={photoClasses}><span className='peer-initials'>{ letters }</span></div>);

        const liClassName = this.props.isSelected ? 'im-dialog-active' : 'im-dialog';

        return (
            <li className={liClassName}>
                <div className='inner-div' onMouseDown={() => this.handleClick()}>
                    {photo}
                    <div className='content-wrap'>
                        <div>{chat.title}</div>
                        <div className='content'><span>{content}</span></div>
                    </div>
                </div>
            </li>
        );
    }
}

export default DialogControl;


