import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import './DialogDetails.css';
import InputBoxControl from "./InputBoxControl";

class DialogDetails extends Component{

    shouldComponentUpdate(nextProps, nextState){
        if (nextProps.history !== this.props.history){
            return true;
        }

        return false;
    }

    handleScroll(){
        const x = ReactDOM.findDOMNode(this.refs.list);
        //console.log('DialogDetails.handleScroll scrollTop=' + x.scrollTop + ' scrollHeight=' + x.scrollHeight + ' offsetHeight=' + x.offsetHeight);

        if (x && (x.scrollTop + x.offsetHeight) >= x.scrollHeight){
            this.props.onLoadNext();
        }
    }

    render(){
        const messages = this.props.history.map(x => <li key={x.id} className='message'>{JSON.stringify(x)}</li>);

        return (
            <div className='details'>
                <div className='list-wrapper'>
                    <ul className='list' ref='list' onScroll={() => this.handleScroll()}>
                        {messages}
                    </ul>
                </div>
                <div className='input-box-wrapper'>
                    <InputBoxControl onSendText={this.props.onSendText} onSendFile={this.props.onSendFile}/>
                </div>
            </div>
        );
    }
}

export default DialogDetails;


