import React, {Component} from 'react';
import './TelegramApp.css';
import Dialogs from './Components/Dialogs';
import DialogDetails from './Components/DialogDetails';
import TdClient from "@arseny30/tdweb/dist/tdweb";
import localForage from "localforage";
import Header from "./Components/Header";

class TelegramApp extends Component{
    constructor(){
        super();

        this.client = new TdClient();
        this.client.onUpdate = update => this.onUpdate(update);
        this.nextKey = 0;
        this.chatControls = new Map();
        this.store = localForage.createInstance({
            name: '/tdlib'
        });
        //this.store.clear();

        this.loading = false;
        this.state = {
            state: 'init',
            chats: [],
            history: [],
        };
    }

    authStateLoop() {
        switch (this.auth_state['@type']) {
            case 'authorizationStateWaitTdlibParameters':
                this.setState({state : 'waitTdLibParameters'});
                this.sendTdParameters();
                break;
            case 'authorizationStateWaitEncryptionKey':
                this.setState({state : 'waitEncryptionKey'});
                this.client.send({ '@type': 'checkDatabaseEncryptionKey' });
                break;
            case 'authorizationStateWaitPhoneNumber':
                this.setState({ state: 'waitPhoneNumber' });
                this.onInput('9997777201');
                break;
            case 'authorizationStateWaitCode':
                this.setState({ state: 'waitCode' });
                this.onInput('22222');
                break;
            case 'authorizationStateReady':
                this.setState({ state: 'ready' });
                this.client
                    .send({
                        '@type': 'getChats',
                        offset_order: 8000000000000000000,
                        limit: 20
                    })
                    .then(result => this.onGetChats(result));
                //this.setHistory([]);

                /*this.client.send({
                  '@type': 'getChats',
                  offset_order: 8000000000000000000,
                  limit: 20
                });*/
                break;
            default:
                this.setState({ state: '???' });
        }
    }

    sendTdParameters() {
        this.client.send({
            '@type': 'setVerbosity',
            verbosity: 2
        });

        this.client.send({
            '@type': 'setTdlibParameters',
            parameters: {
                '@type': 'tdParameters',
                use_test_dc: true,
                api_id: 12183,
                api_hash: '41c3080d9028cf002792a512d4e20089',
                system_language_code: 'en',
                device_model: 'Desktop',
                system_version: 'Unknown',
                app_version: 'tdclient-emscripten',
                use_secret_chats: false,
                use_message_database: true,
                use_file_database: false,
                database_directory: '/db',
                files_directory: '/'
            },
            extra: {
                a: ['a', 'b'],
                b: 123
            }
        });
    }

    onUpdate(update) {
        switch (update['@type']) {
            case 'updateAuthorizationState':
                this.onUpdateAuthState(update.authorization_state);
                break;
            case 'updateNewChat':
                //this.onUpdateChatTitle(update.chat.id, update.chat.title);
                break;
            case 'updateChatTitle':
                //this.onUpdateChatTitle(update.chat_id, update.title);
                break;
            case 'updateNewMessage':
                this.onUpdateNewMessage(update.message);
                break;
            case 'updateMessageSendSucceeded':
                this.updateMessageSendSucceeded(update.old_message_id, update.message);
                break;
            case 'updateChatLastMessage':
                this.updateChatLastMessage(update.chat_id, update.order, update.last_message);
                break;
            case 'updateFile':
                this.onUpdateFile(update.file);
                break;
            default:
                break;
        }
    }

    updateChatLastMessage(chat_id, order, last_message){
        if (!last_message) return;
        if (order === "0") return;

        let updatedChats = this.state.chats.map(x =>{
            return x.id !== chat_id ? x : Object.assign({}, x, {'last_message' : last_message, 'order' : order });
        });

        const orderedChats = updatedChats.sort((a, b) => { return a.order < b.order; });

        this.setState({chats : orderedChats});
    }

    onUpdateNewMessage(message){
        if (!this.chat) return;
        if (this.chat.id !== message.chat_id) return;

        this.historyPushFront(message);
    }

    updateMessageSendSucceeded(old_message_id, message){
        if (!this.chat) return;
        if (this.chat.id !== message.chat_id) return;

        let updatedHistory = this.state.history.map((obj) =>{
            return obj.id === old_message_id ? message : obj;
        });

        this.setHistory(updatedHistory);
    }

    onUpdateAuthState(auth_state) {
        this.auth_state = auth_state;
        this.authStateLoop();
    }

    onGetChats(result){
        let chats = [];
        for (let i = 0; i < result.chat_ids.length; i++){
            chats.push(this.client.send({
                '@type' : 'getChat',
                chat_id : result.chat_ids[i]
            }));
        }

        Promise.all(chats).then(result => this.onGetChats2(result));
    }

    onGetChats2(result){
        this.setState({ chats : result });

        for (let i = 0; i < result.length; i++){
            let chat = result[i];
            let [id, pid, idb_key] = this.getChatPhoto(chat);
            if (pid) {
                chat.pid = pid;
                if (idb_key) {
                    chat.idb_key = idb_key;
                    this.getLocalFile(chat, idb_key);

                } else {
                    this.client.send({ '@type': 'downloadFile', file_id: id, priority: 1 });
                }
            }
        }
    }

    getChatPhoto(chat) {
        if (chat['@type'] !== 'chat') {
            return [0, '', ''];
        }
        if (chat.photo) {
            let file = chat.photo.small;
            if (file.remote.id) {
                return [file.id, file.remote.id, file.idb_key];
            }
        }
        return [0, '', ''];
    }

    onUpdateFile(file) {
        if (!file.idb_key || !file.remote.id) {
            return;
        }
        let pid = file.remote.id;
        let idb_key = file.idb_key;
        for (let obj of this.state.chats) {
            if (obj.pid === pid && obj.idb_key !== idb_key) {
                obj.idb_key = idb_key;
                this.getLocalFile(obj, idb_key);
            }
        }
    }

    getLocalFile(obj, idb_key) {
        this.store.getItem(idb_key).then(blob => {
            console.log('Got blob: ' + idb_key + ' => ' + blob);
            //obj.blob = blob;

            if (blob){
                let updatedChats = this.state.chats.map(x =>{
                    return x === obj ? Object.assign({}, x, { blob : blob}) : x;
                });
                this.setState({ chats : updatedChats });
            }
        });
    }

    selectChat(chat){
        let chat_id = chat.id;
        this.chat = chat;
        this.client
            .send({
                '@type': 'getChatHistory',
                chat_id: chat_id,
                from_message_id: 0,
                offset: 0,
                limit: 20
            })
            .then(result => {
                /*for (let message of result.messages) {
                    this.historyPushBack(message);
                }*/
                //this.setHistory([]);
                this.setHistory(result.messages);
            });
    }

    setHistory(history) {
        this.setState({ history: history });
    }

    appendHistory(history) {
        if (history.length === 0) return;

        this.setState({ history: this.getHistory().concat(history) });
    }

    getHistory() {
        return this.state.history;
    }

    historyPushFront(entry) {
        //this.prepareEntry(entry);
        this.setHistory([entry].concat(this.getHistory()));
    }

    historyPushBack(entry) {
        //this.prepareEntry(entry);
        this.setHistory(this.getHistory().concat([entry]));
    }

    onSendText(text){
        if (!this.chat) return;
        if (!text) return;

        this.client.send({
            '@type': 'sendMessage',
            chat_id: this.chat.id,
            input_message_content: {
                '@type': 'inputMessageText',
                text: { '@type': 'formattedText', text: text }
            }
        });
    }

    onSendFile(file){
        if (!this.chat) return;
        if (!file) return;

        this.client.send({
            '@type': 'sendMessage',
            chat_id: this.chat.id,
            input_message_content: {
                '@type': 'inputMessageDocument',
                document: { '@type': 'inputFileBlob', name: file.name, blob: file }
            }
        });
        /*.then(result => {
            result.blob = file;
            this.historyPushBack(result);
        });*/
    }

    onLoadNext(){
        if (!this.chat) return;
        if (this.loading) return;

        let fromMessageId = 0;
        if (this.state.history && this.state.history.length > 0){
            fromMessageId = this.state.history[this.state.history.length - 1].id;
        }

        this.loading = true;
        this.client
            .send({
                '@type': 'getChatHistory',
                chat_id: this.chat.id,
                from_message_id: fromMessageId,
                offset: 0,
                limit: 20
            })
            .then(result => {
                    this.loading = false;
                    this.appendHistory(result.messages);
                },
                () =>{
                    this.loading = false;
                });
    }

    onInput(line) {
        switch (this.state.state) {
            case 'wait':
                return;
            case 'waitPhoneNumber':
                this.historyLog('Set phone number = ' + line);
                this.client
                    .send({ '@type': 'setAuthenticationPhoneNumber', phone_number: line })
                    .catch(this.onAuthError);
                this.setState({ state: 'sendPhoneNumber' });
                break;
            case 'waitCode':
                this.historyLog('Check code = ' + line);
                this.client
                    .send({
                        '@type': 'checkAuthenticationCode',
                        code: line,
                        first_name: 'A',
                        last_name: 'B'
                    })
                    .catch(this.onAuthError);
                this.setState({ state: 'sendCode' });
                break;
            //case 'Ok':
                //this.onCommand(line);
            //    break;
            //case 'chat':
                //this.onChatCommand(line);
            //    break;
            default:
                this.historyLog('IGNORED: ' + line);
                break;
        }
        //this.input.value = '';
    }

    historyLog(text) {
        //this.historyPushBack({ '@type': 'log', text: text });
    }

    render(){
        return (
            <div>
                <Header status={this.state.state}/>
                <div className='im-page-wrap'>
                    <Dialogs
                        className='master'
                        store={this.store}
                        client={this.client}
                        chats={this.state.chats}
                        selectedChat={this.chat}
                        onSelectChat={chat => this.selectChat(chat)}/>
                    <DialogDetails
                        className='details'
                        history={this.state.history}
                        onSendText={text => this.onSendText(text)}
                        onSendFile={file => this.onSendFile(file)}
                        onLoadNext={() => this.onLoadNext()}
                    />
                </div>
                <div className='footer'></div>
            </div>
        );
    }
}

export default TelegramApp;


